import React, { useState, useEffect } from 'react';
import { Container } from 'rsuite';
import { useNavigate } from 'react-router-dom';
import { validate } from "../../../Helpers/sanitize";
import { register } from "../../../services/API/user";
import { Form, ButtonToolbar, Button } from 'rsuite';

function Register() {

    const [errorVisible, setErrorVisible] = useState(false);
    const [userExist, setUserExist] = useState(false);
    const [toShort, setToShort] = useState(false);
    const [errorPlacement] = useState('rightStart');
    const errorMessage = errorVisible ? 'Saisie obligatoire' : userExist ? "Utilisateur xistant" : toShort ? "Mot de passe trop court" : null;

    const navigate = useNavigate();

    const [inputs, setInputs] = useState({ username: "", secretKey : "", password: "", passwordConfirm: ""});

    const handleRegister = async (e) => {
        e.preventDefault();
        const inputsValidation = validate(inputs);
        if (inputs.username === "" || inputs.secretKey === "" || inputs.password === "" || inputs.passwordConfirm === "") {
            setErrorVisible(true);
        }
        if(inputsValidation === true) {
            if (inputs.password === inputs.passwordConfirm) {
                const res = await register(inputs);
                if (res.status === 409) {
                    setUserExist(true);
                    return;
                } else {
                    navigate("/success");
                    const checkInscription = localStorage.getItem("checkInscription");
                    if (checkInscription === null) {
                        localStorage.setItem("checkInscription", true);
                    }
                }
            }
        } else if (inputsValidation === "toShort") {
            setToShort(true);
        } else {
            setErrorVisible(true);
        }
    }

    useEffect(() => {
        const timer = setTimeout(() => {
            setErrorVisible(false);
            setUserExist(false);
            setToShort(false);
        }, 4000);

        return () => clearTimeout(timer);
    }, [errorVisible, userExist, toShort]);

    return (
        <main>
            <Container className='register-container'>
                <article className='register-infos'>
                    <h1>L'inscription</h1>
                    <p>Pour rejoindre l'aventure c'est une tape obligatoire !</p>
                </article>

                <aside className='register-note'>
                    <h1>NOTE</h1>
                    <p>Toutes vos donnes sont hashes et scurises, ne les communiquez  personnes sous aucun prtexte.</p>
                    <p>Si vous perdez votre mot de passe, il vous sera demand via le support votre cl secrte pour vrifier vos donnes pour la rcupration de compte.</p>
                </aside>

                <Form layout="horizontal" onChange={(formValue) => setInputs(formValue)} className='form-register'>
                    <Form.Group controlId="username">
                        <Form.Control name="username" placeholder='Nom de compte' errorMessage={errorMessage} errorPlacement={errorPlacement}/>
                    </Form.Group>

                    <Form.Group controlId="secretKey">
                        <Form.Control name="secretKey" placeholder='Cl secrte (8 caractres minimum)' errorMessage={errorMessage} errorPlacement={errorPlacement}/>
                        <Form.HelpText tooltip>Vous pouvez mettre ce que vous voulez. Gardez l bien elle vous sera demande en cas de rcupration de mot de passe.</Form.HelpText>
                    </Form.Group>

                    <Form.Group controlId="password">
                        <Form.Control name="password" placeholder='Mot de passe (8 caractres minimum)' errorMessage={errorMessage} errorPlacement={errorPlacement} type="password"/>
                    </Form.Group>

                    <Form.Group controlId="passwordConfirm">
                        <Form.Control name="passwordConfirm" placeholder='Confirmation mot de passe' errorMessage={errorMessage} errorPlacement={errorPlacement} type="password"/>
                    </Form.Group>

                    <ButtonToolbar>
                        <Button appearance="primary" onClick={handleRegister}>S'inscrire</Button>
                    </ButtonToolbar>
                </Form>
            </Container>
        </main>
    )
}

export default Register;